.valores .card {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 30rem;
  height: 35rem;
  box-shadow: 10px 10px 5px 0px rgba(191, 186, 186, 0.75);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(191, 186, 186, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(191, 186, 186, 0.75);
}

.valores .card .face {
  position: absolute;
  backface-visibility: hidden;
  transition: transform 0.5s;
  overflow: hidden;
}

.valores .card .front {
  transform: perspective(600px) rotateY(0deg);
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
}
.valores .card .front img {
  width: 20rem;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

.valores .front .etiqueta-valor {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #6a3357;
  color: #fff;
}
.valores .front .etiqueta-valor h3 {
  line-height: 0;
}

.valores .card .back {
  transform: perspective(600px) rotateY(180deg);
  background-color: #6a3357;
  height: 100%;
  color: #fff;
  padding: 2rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.valores .card .back p {
  color: #fff;
  font-size: 1.5rem;
}

.valores .card:hover .front {
  transform: perspective(600px) rotateY(180deg);
}

.valores .card:hover .back {
  transform: perspective(600px) rotateY(360deg);
}

